@media screen {
  body {
    font-family: 'Courier New', Courier, monospace;
  }

  .page {
    width: 100%;
    margin: 0.2em 0.1em;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }

  div.label {
    padding: 3mm;
    border: 1px dashed;
    height: 22mm;
  }
}

@media print {
  @page {
    size: 210mm 297mm;
    margin: 8mm 6mm;
    padding: 0;
  }

  body {
    margin: 0;
    padding: 0;
  }

  .page {
    box-sizing: border-box;
    width: 210mm;
    height: 297mm;
    margin: 0;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(11, 1fr);
    page-break-after: always;
    break-after: page;
  }

  #input {
    display: none;
  }

  div.label {
    box-sizing: border-box;
    font-family: 'Courier New', Courier, monospace;
    font-size: 16px;
    margin: 0;
    padding: 0;
    padding: 3mm;
  }

  div.label-content {
  }
}
